@media screen and (max-width: 1024px) {

    h1,
    .h1 {
        font-size: 32px;
    }

    h2,
    .h2 {
        font-size: 36px;
    }

    h3,
    .h3 {
        font-size: 24px;
    }

    h4,
    .h4 {
        font-size: 22px;
    }

    h5,
    .h5 {
        font-size: 16px;
    }

    h6,
    .h6 {
        font-size: 16px;
    }



    .has-bg {
        @include default-bg;
    }

    .container {
        padding-right: $container-mobile-padding;
        padding-left: $container-mobile-padding;
    }

    .container-xxl {
        padding: 0;
    }

    .header {
        padding: 18px 0 8px;

        &::before {
            content: "";
            position: absolute;
            left: 0;
            right: 0;
            top: 0;
            height: 72px;
            background-color: $bg-primary;
            z-index: 2;
        }
    }

    .header-logo-link {
        z-index: 2;
    }

    .header-logo-img {
        max-width: 126px;
    }

    .header-nav {
        flex-direction: column;
        background-color: $bg-secondary;
        position: absolute;
        z-index: 1;
        top: -510px;
        left: 0;
        padding: 41px 0 60px 0;
        transition: .7s;
        box-shadow: 14px 22px 40px 0 #1e23429c;

        &.show {
            top: 72px
        }


        &__list {
            flex-direction: column;
            align-items: center;
            margin-bottom: 38px;
        }

        .btn-primary {
            padding: 22px 23px;
            max-width: 295px;
            width: 100%;
            justify-content: center;
            margin: 0 auto;
        }
    }

    .header-list-link {
        font-size: 24px;
        line-height: 184%;
        padding: 0;
    }

    .hamburger {
        z-index: 2;
    }

    section.first-screen {
        margin-bottom: 55px;
    }

    .first-screen {
        .container {
            text-align: center;
            display: flex;
            flex-direction: column;
            align-items: center;
            padding: 26px 40px;
            position: relative;
            z-index: 1;
        }

        .slick-track:before {
            content: "";
            width: 100%;
            height: 100%;
            background: #04091199;
            position: absolute;
            left: 0;
            top: 0;
        }
    }

    .banner-item {
        border-radius: 0;
    }

    .banner-title,
    .banner-subtitle {
        max-width: 260px;
    }

    .banner-title {
        margin-bottom: 8px;
    }

    .banner-subtitle {
        margin-bottom: 22px;
    }

    .calculation-form {
        min-width: initial;
        padding: 52px 27px 34px;
        margin: 0 auto;

        .row {
            flex-direction: column;
        }
    }


    .calculation-decoration,
    .template-title.col-lg-7 h2:after {
        display: none;
    }


    .template-title h2 {
        font-size: 24px;
        text-align: center;
    }

    h3 {
        font-size: 16px;
    }

    .calculation {

        /*type 2*/
        .template-title {

            &.col-lg-7 {
                margin-bottom: 56px;

                &:after {
                    display: inline-block;
                }

                h2 {
                    &:after {
                        display: none;
                    }
                }
            }
        }
    }

    .col.controled-col {
        padding-top: 7px;
    }

    .input-wrapper.controled-wrapper {
        margin-bottom: 8px;
    }

    .input-wrapper.file-wrapper {
        padding-top: 35px;
    }

    .mobile-center {
        text-align: center;
    }

    .slider-template {
        max-width: 335px;

        .slick-list {
            margin-bottom: 32px;
        }

        .slick-slide {
            padding: 0;
        }

        .template-card {
            padding: 0 23px 27px;

            .img-box {
                margin: 0 -23px 29px;

                img {
                    height: 213px;
                }
            }
        }

        .template-card-row {
            gap: 13px;

            .btn {
                padding: 13px 5px;
            }
        }

    }

    .services {

        .slider-template {
            .template-card {
                .img-box {
                    img {
                        height: 100%;
                        object-fit: initial;
                    }
                } 
            } 
        } 
    }
}
